import { Component, OnInit, EventEmitter, ElementRef, TemplateRef, ViewChild } from '@angular/core';
import { GSPMetadataService } from '@gsp-lcm/metadata-selector';
import { MetadataService } from '../../metadata.service';
import { MetadataDto } from '@gsp-lcm/metadata-selector';
// import { TreeModel } from '../../../../packages/file-tree-panel-package/models/tree-model';

@Component({
  selector: 'app-create-metadata',
  templateUrl: './createmetadata.component.html',
  styleUrls: ['./createmetadata.component.css']
})
export class CreatemetadataComponent implements OnInit {
  @ViewChild('btn') btns: TemplateRef<any>;
  path: string;
  closeModalEmitter: EventEmitter<any>;
  createMetadataSuccess: EventEmitter<any>;
  classifier: string;
  projInfo: any;
  metadata = {
    nameSpace: '',
    code: '',
    name: '',
    bizobjectID: '',
    boCode: '',
    location: ''
  };

  selectBO: EventEmitter<any> = new EventEmitter();
  createMetadataEvent: EventEmitter<any> = new EventEmitter();
  openMetadataGuideEvent: EventEmitter<any> = new EventEmitter();

  // tslint:disable-next-line:max-line-length
  constructor(private metadataService: MetadataService, private gspMetadataService: GSPMetadataService) {
    this.closeModalEmitter = new EventEmitter<any>();
    this.createMetadataSuccess = new EventEmitter<any>();
  }

  ngOnInit() {
  }

  public initNameSpace() {
    // this.metadata.location = this.path.replace(/\\/g, '/');
    // this.gspMetadataService.GetProjectInfo(this.path.substring(1)).subscribe(data => {
    //   this.projInfo = data;
    //   if (this.projInfo) {
    //     this.metadata.nameSpace = this.projInfo.nameSpace;
    //   }
    // });
    this.metadata.location = this.path.replace(/\\/g, '/');
    this.gspMetadataService.GetProjectInfo(this.path.substring(1)).subscribe(data => {
      this.projInfo = data;
      if (this.projInfo) {
        this.metadata.nameSpace = this.projInfo.nameSpace;
      }
      this.gspMetadataService.GetGspProjInfo(this.projInfo.projectPath).subscribe(item => {
        if (item) {
          this.metadata.bizobjectID = item.bizobjectID;
          this.metadata.boCode = item.bizobjectCode;
        }
      });
    });
  }

  setCode(code: string) {
    if (!this.metadata.code) {
      if (code.toLowerCase() === 'gspbusinessentity') {
        this.metadata.code = this.metadata.boCode;
      }
    }
    if (!this.metadata.name) {
      if (code.toLowerCase() === 'gspbusinessentity') {
        this.metadata.name = this.metadata.boCode;
      }
    }
    // this.gspMetadataService.GetProjectInfo(this.path.substring(1)).subscribe(proj => {
    //   this.metadataService.getMetadataStoragePath().data.forEach(item => {
    //     if (code.toLowerCase() === item.type.toLowerCase()) {
    //       this.metadata.location = '/' + proj.projectPath + '/' + item.path;
    //     }
    //   });
    // });
  }

  // 新建元数据
  createMetadata() {
    // 缓存下来的选择的元数据类型信息
    const index = this.metadataService.selectedMetadataType.length;
    // 正则表达式
    const reg = new RegExp('^[a-zA-Z0-9.\x7f-\xff]*$');
    const reg2 = new RegExp('^[a-zA-Z_\x7f-\xff][a-zA-Z0-9_\x7f-\xff]*$');
    let metadatatype = '';
    if (index > 0) {
      metadatatype = this.metadataService.selectedMetadataType[index - 1];
    }
    if (!metadatatype) {
      alert('请选择要创建的元数据类型！');
      return;
    }
    // 输入校验
    if (!this.metadata.nameSpace) {
      alert('元数据命名空间不能为空！');
      return;
    } else if (!reg.test(this.metadata.nameSpace)) {
      alert('元数据命名空间包含非法字符，请重新输入');
      return;
    }
    if (!this.metadata.code) {
      alert('元数据编号不能为空！');
      return;
    } else if (!reg2.test(this.metadata.code)) {
      alert('元数据编号包含非法字符，请重新输入');
      return;
    }
    if (!this.metadata.name) {
      alert('元数据名称不能为空！');
      return;
    }
    if (!this.metadata.bizobjectID) {
      alert('业务对象不能为空！');
      return;
    }
    this.closeModalEmitter.emit();
    let postfix = '';
    this.gspMetadataService.GetMetadataTypeList().subscribe(typeList => {
      // tslint:disable-next-line:no-shadowed-variable
      typeList.forEach(type => {
        if (metadatatype.toLowerCase() === type.typeCode.toLowerCase()) {
          postfix = type.postfix;
        }
      });
      const fileName = this.metadata.code + postfix;
      const relativePath = this.metadata.location.replace(/\\/g, '/').substring(1);

      // wj20180807-be设计器向导开始
      if (postfix === '.be' || postfix === '.udt'||postfix==='.coderule') {
        // tslint:disable-next-line:max-line-length
        const guideData = new MetadataDto('', this.metadata.nameSpace, this.metadata.code, this.metadata.name, fileName, metadatatype, this.metadata.bizobjectID, relativePath, '', '');
        this.openMetadataGuideEvent.emit(guideData);
        return;
      }
      // wj20180807-be设计器向导结束

      // 初始化
      // tslint:disable-next-line:max-line-length
      const metadatadto = new MetadataDto('', this.metadata.nameSpace, this.metadata.code, this.metadata.name, fileName, metadatatype, this.metadata.bizobjectID, relativePath, '', '');
      this.gspMetadataService.InitializeMetadataEntity(metadatadto).subscribe(response => {
        metadatadto.content = response.content;
        metadatadto.id = response.id;
        this.gspMetadataService.CreateMetadata(metadatadto).subscribe(data => {
          alert('新建成功！');
          // 刷新树
          this.createMetadataSuccess.emit();
          // 发送CreateMetadata事件给workspace接收
          this.createMetadataEvent.emit('/' + relativePath + '/' + fileName);
          // 创建成功去加载树
          // this.treeModel.addFileNode(metadatadto.id, fileName, path.replace(/\\/g, '/') + '/' + fileName);
          // 清缓存
          this.clearCache();
        }, error => {
          alert('新建失败！');
          // 清缓存
          this.clearCache();
        });
      }, error => {
        alert('创建失败！');
        // 清缓存
        this.clearCache();
      });
    });
  }

  private clearCache() {
    this.metadata = {
      nameSpace: '',
      code: '',
      name: '',
      bizobjectID: '',
      boCode: '',
      location: ''
    };
    // 清空缓存
    this.metadataService.clearMetadatas();
  }

  public hideModal(): void {
    this.closeModalEmitter.emit();
  }

  onSelectedChange(classifier: string) {
    this.classifier = classifier;
  }

  onSelectBO() {
    // alert('on');
    // this.selectBO.emit();
  }

  setBO(data: any) {
    this.metadata.bizobjectID = data.id;
  }
}



