import { MetadataWizardPackageView } from './views';
import { ComponentFactoryResolver, ComponentRef, Injector, Type } from '@angular/core';
import { CreatemetadataComponent } from './views/createmetadate/createmetadata.component';
import { GSPMetadataService } from '@gsp-lcm/metadata-selector';
import { Package, GSP } from '@farris/ide-devkit';

export class MetadataWizardPackage extends Package {
  metadataService: GSPMetadataService;
  constructor(gsp: GSP, private cfr: ComponentFactoryResolver, private injector: Injector) {
    super(gsp);
    this.metadataService = this.injector.get(GSPMetadataService);
  }

  activate(state) {
    this.gsp.eventBus.on(null, null, 'Command:newMetadata', this, () => this.showModal());
    this.gsp.eventBus.on(null, null, 'Command:deleteMetadata', this, () => this.deleteMetadata());
    this.gsp.eventBus.on(null, null, 'Command:pack', this, () => this.pack());
  }

  showModal() {
      const packageViewT = new MetadataWizardPackageView({}, this.cfr, this.injector);
      const title = '新建元数据';
      const comp = packageViewT.getElement().instance as CreatemetadataComponent;
      this.gsp.eventBus.requestFor('FileTreePanelPackage', 'metadataCreate', 'AccessFileTree', {action: '6'}, (nodeInfo) => {
        if (!nodeInfo.nodePath) {
          return;
        }
        if (nodeInfo.nodeType === 'folder' || nodeInfo.nodeType === 'project') {
          comp.path = nodeInfo.nodePath;
          comp.initNameSpace();
        } else if (nodeInfo.nodeType === 'file') {
          const handledPath = nodeInfo.nodePath.replace(/\\/g, '/');
          comp.path = handledPath.substring(0, handledPath.lastIndexOf('/'));
          comp.initNameSpace();
        } else {
          console.log('unknowed type');
          alert('请选择合适的存放目录！');
          return;
        }
      });
      if (comp) {
        comp.createMetadataSuccess.subscribe(() => { this.refreshTree(); });
        comp.createMetadataEvent.subscribe((uri) => this.openMetadata(uri));
        comp.openMetadataGuideEvent.subscribe((data) => this.oepnMetadataGuide(data));
      }
      const modalPanel = this.gsp.workspace.addModalPanel({item: {
        element: packageViewT.getElement(),
        modalOptions: {
          title: title,
          width: 823,
          height: 623,
          buttons: comp.btns
        }
      }});
      comp.closeModalEmitter.subscribe(() => modalPanel.destroy());
  }
  getComponent(): Type<any> {
    return CreatemetadataComponent;
  }

  private createMetadata(data) {
    this.gsp.eventBus.post(MetadataWizardPackage, this.id, 'Command:CreateMetadata', data);
  }

  private openMetadata(uri) {
    this.gsp.workspace.open(uri);
  }

  /**
   * 打开元数据向导
   * @param data
   */
  private oepnMetadataGuide(data) {
    this.gsp.eventBus.post(MetadataWizardPackage, this.id, 'Command:openMetadataGuide:' + data.type, data);
  }

  refreshTree() {
    this.gsp.eventBus.requestFor('FileTreePanelPackage', 'metadataCreate', 'AccessFileTree', {action: '5'}, () => {
      console.log('event has been deal done');
    });
  }

  deleteFileTree() {
    this.gsp.eventBus.requestFor('FileTreePanelPackage', 'projectCreate', 'AccessFileTree', {action: '4'}, () => {
      console.log('event has been deal done');
    });
  }

  deleteMetadata() {
    this.gsp.eventBus.requestFor('FileTreePanelPackage', 'projectCreate', 'AccessFileTree', { action: '6' }, (nodeInfo) => {
      console.log(nodeInfo);
      if (nodeInfo.nodeType === 'file') {
        this.metadataService.DeleteMetadataOrDir(nodeInfo.nodePath.substring(1)).subscribe(data => {
          alert('删除成功');
          // 删除树节点
          this.deleteFileTree();
        }, error => {
          alert('删除失败');
        });
      }
    });
  }

  pack() {
    this.gsp.eventBus.requestFor('FileTreePanelPackage', 'projectCreate', 'AccessFileTree', { action: '6' }, (nodeInfo) => {
      console.log(nodeInfo);
      if (nodeInfo.nodeType === 'project') {
        this.metadataService.PackageBuild(nodeInfo.nodePath.substring(1));
        // 刷新树节点
        this.refreshTree();
      }
    });
  }
}



