import {ComponentFactoryResolver, Injector} from '@angular/core';
import {CreatemetadataComponent} from './createmetadate/createmetadata.component';
import { PackageView } from '@farris/ide-devkit';
export class MetadataWizardPackageView extends PackageView {
  constructor(serializedState: any, private cfr: ComponentFactoryResolver, private injector: Injector) {
    super(serializedState);
    this.element = cfr.resolveComponentFactory(CreatemetadataComponent).create(injector);
  }
}



